import React, { Component } from 'react';
import './Post.css';
import account from '../img/account.png';

class Post extends Component {
    state = {  }
    render() { 
        return ( 
            <div className="post">
                <div className="post-head">
                    <img className="account" src={account} alt="" width="100%" height="100%"/>
                    <p>Abd alrahman daas</p>
                    <button>=</button>
                </div>
                <div className="bio">
                    <p className="date">6/27/2021</p>
                    <p className="size">27 MB</p>
                    <p className="type">photo</p>
                </div>
                <div className="img">
                    <img src={this.props.image} alt="" width="100%" height="100%"/>
                </div>
                <div className="reaction">
                <button>click</button>
                <button>click</button>
                <button>click</button>
                </div>
            </div>
        );
    }
}

export default Post;